import { Outlet } from './Outlet';
import { defineComponent, h, renderSlot } from 'vue';
export const Route = defineComponent({
    name: 'Route',
    props: {
        path: {
            default: '',
            required: false,
            type: String,
        },
        caseSensitive: {
            default: false,
            required: false,
            type: Boolean,
        },
        element: {
            default: () => h(Outlet),
            required: false,
            type: Object,
        },
    },
    setup(props, { slots }) {
        return () => renderSlot(slots, 'element', undefined, () => [props.element]);
    },
});
